import appTimeScreening from "../../../components/page-component/app-time-screening/app-time-screening.vue";
import orderMenu from "../components/order-menu.vue";



import { mapState } from "vuex";
export default {
    data() {
        return {
            status: 1,
            num: '-1',
            getFocus: false,
            more_list: false,
            cityList: [],
            deliveryId: '0',
            delivery: '',
            addressId: '0',
            refund_price: 0,
            theme: {
                color: '#446dfd'
            },
            page: 1,
            menu: [
                {
                    name: '待收货',
                    value: '2'
                },
                {
                    name: '待退款',
                    value: '4'
                },
                {
                    name: '已收货',
                    value: '9'
                },
                {
                    name: '已完成',
                    value: '3'
                }
            ],
            active: null,
            show: false,
            search: false,
            keyword: '',
            list: [],
            candidate: [],
            date_start: '',
            date_end: '',
            time: 0,
            inSearch: false,
            address: [],
            chooseTime: false,
            msg: false,
            searchResult: false,
            total: 0,
            about: '',
            iphone_x: false,
            loading: false
        }
    },
    components: {



        "app-time-screening": appTimeScreening,
        "order-menu": orderMenu
    },
    computed: {
        ...mapState({
            userInfo: state => state.user.info,
            adminImg: state => state.mallConfig.__wxapp_img.app_admin
        })
    },
    onReachBottom() {
        this.getMore();
    },
    methods: {
        cancel(e) {
            if(e && e.id) {
                for(let i in this.list) {
                    if(this.list[i].id == e.id) {
                        this.list[i] = e;
                        this.$forceUpdate();
                    }
                }
            }
            this.chooseTime = false;
        },
        toCall(e) {
            e.showMobile = true;
        },
        update(row) {
            if(row == 0) {
                this.page = 1;
                uni.showLoading({
                    mask: true,
                    title: '加载中...'
                });
                this.getList();
            }else {
                for(let index in this.list) {
                    if(this.list[index].id == row.id) {
                        this.list[index] = row;
                        this.$forceUpdate();
                    }
                }
            }
        },
        clearSearch() {
            this.keyword = '';
        },
        reload() {
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            this.page = 1;
            this.list = [];
            this.getList();
        },

        toDetail(order_id,status) {
            uni.navigateTo({
                url: '/pages/app_admin/order-detail/order-detail?id='+order_id+'&status='+status
            })
        },
        toRedirect(url) {
            uni.redirectTo({
                url: url
            })
        },
        // 切换类别
        tabStatus(e) {
            let that = this;
            that.status = e;
            that.active = null;
            that.list = [];
            that.date_start = '';
            that.date_end = '';
            that.keyword = '';
            that.time = 0;
            that.show = false;
            that.num = '-1';
            that.page = 1;
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            that.getList();
        },
        toSearch() {
            this.search = true;
            this.list = [];
            this.searchResult = false;
            this.candidate = this.$storage.getStorageSync('keyword') ? this.$storage.getStorageSync('keyword') : [];
            this.inSearch = false;
        },
        keywordSearch(e) {
            this.keyword = e;
            this.searchResult = true;
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            this.page = 1;
            this.getList();
        },
        // 清除搜索记录
        clear() {
            let that = this;
            this.$storage.removeStorageSync('keyword');
            that.candidate = [];
            uni.showToast({
                title: '清理成功',
                duration: 1000,
                type: 'success',
                mask: false
            });
        },
        // 搜索
        searchMethod() {
            let value = this.$storage.getStorageSync('keyword') ? this.$storage.getStorageSync('keyword') : [];
            if (this.keyword.length === 0) {
                return
            } else if (value.length > 0) {
                value.unshift(this.keyword)
            } else {
                value = [this.keyword]
            }
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            this.page = 1;
            this.getList();
            value.forEach(function(row, index) {
                if (value[0] == value[index] && index > 0) {
                    value.splice(index, 1);
                }
            })
            this.$storage.setStorageSync('keyword', value);
            this.inSearch = true;
            this.searchResult = true;
        },
        // 取消搜索
        cancelSeacrch() {
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            this.search = false;
            this.keyword = '';
            this.page = 1;
            this.getList();
        },
        // 确认选择时间
        toChoose(res) {
            let that = this;
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            that.date_start = res.date_start;
            that.date_end = res.date_end;
            that.time = res.choose;
            that.page = 1;
            that.getList();
            that.chooseTime = false;
        },
        // 打开时间筛选
        toTime() {
            let that = this;
            that.chooseTime = !that.chooseTime;
            that.show = false;
        },
        // 选择更多状态
        chooseItem(e) {
            let that = this;
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            that.num = e;
            that.menu.forEach(function(row, index) {
                if (row.value == that.num) {
                    that.active = row;
                }
            })
            that.show = !that.show;
            that.page = 1;
            that.list = [];
            that.getList();
        },
        // 切换状态
        tab(e) {
            this.num = e;
            this.show = false;
            this.active = null;
            this.list = [];
            this.keyword = '';
            this.page = 1;
            uni.showLoading({
                mask: true,
                title: '加载中...'
            });
            this.getList();
        },
        getList() {
            let that = this;
            if(that.loading) {
                return false
            }
            that.loading = true;
            that.about = '';
            let url;
            if (this.status == 1) {
                url = that.$api.app_admin.order
            } else if (this.status == 2) {
                url = that.$api.app_admin.refund
            }
            that.$request({
                url: url,
                data: {
                    status: that.num,
                    date_end: that.date_end,
                    date_start: that.date_start,
                    page: that.page,
                    keyword: that.keyword
                }
            }).then(response=>{
                that.$hideLoading();
                uni.hideLoading();
                that.loading = false;
                if(response.code == 0) {
                    let list = response.data.list;
                    if (that.status == 2) {
                        let status = [];
                        list.forEach(function(row) {
                            row.showMobile = false;
                            row.detail = [row.detail];
                            status = row.status_cn.split(' ');
                            row.refund_status_cn = status[status.length - 1];
                        })
                        let address = response.data.address;
                        address.forEach((row) => {
                            row.address = row.address.replace(/"/g, '');
                            row.address = row.address.replace(/,/g, '');
                            row.address = row.address.replace('[', '');
                            row.address = row.address.replace(']', '');
                        })
                        that.address = address;
                    } else {
                        list.forEach(function(row) {
                            row.showMobile = false;
                            row.detail.forEach(function(res) {
                                if (res.refund_status == 1 || res.refund_status == 2) {
                                    row.have_refund = 1;
                                }
                            })
                        })
                    }
                    that.more_list = false;
                    if (list.length == response.data.pagination.pageSize) {
                        that.more_list = true;
                    }
                    if(that.page == 1) {
                        that.list = list;
                    }else {
                        that.list = that.list.concat(list);
                    }
                    that.page++;
                    that.$forceUpdate();
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(() => {
                that.loading = false;
                that.$hideLoading();
                uni.hideLoading();
            });
        },
        toRefundOrder(e) {
            let that = this;
            that.list = [];
            that.more_list = false;
            that.keyword = e;
            that.status = 2;
            that.search = true;
            that.searchMethod();
        },
        getMore() {
            if (this.more_list && (this.searchResult && this.search) || !this.search) {
                this.getList();
            }
        },
        toSendType() {
            let that = this;
            let para;
            if (that.sendType == 1 || that.detail.send_type == 2) {
                uni.showLoading({
                    mask: true,
                    title: '加载中...'
                });
                para = {
                    is_express: 0,
                    words: '',
                    order_id: that.detail.id
                };
                if(that.detail.send_type == 2) {
                    para.man = that.delivery;
                }
                that.$request({
                    url: that.$api.app_admin.send,
                    data: para,
                    method: 'post'
                }).then(response => {
                    uni.hideLoading();
                    if (response.code === 0) {
                        uni.showToast({
                            title: response.msg,
                            type: 'success',
                            mask: false,
                            duration: 2000
                        });
                        that.page = 1;
                        that.isSend = false;
                        that.sendType = 0;
                        that.getList();
                    } else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }).catch(response => {
                    uni.hideLoading();
                    uni.showToast({
                        title: response,
                        icon: 'none',
                        duration: 1000
                    });
                });
            }
        },
        look(e) {
            uni.previewImage({
                current: e, // 当前显示图片的http链接
                urls: [e] // 需要预览的图片http链接列表
            })
        },
        lookAbout(e) {
            this.detail = e;
            this.isReason = true;
        },
        toConfirm(e) {
            this.detail = e;
            this.confirmOrder = !this.confirmOrder;
        },
        toRefund(e) {
            this.detail = e;
            this.refund_price = e.refund_price;
            this.isRefund = !this.isRefund;
        },
        agree() {
            let that = this;
            uni.showLoading({
                mask: true,
                title: '处理中...'
            });
            if (that.detail.refund_price > 0) {
                that.$request({
                    url: that.$api.app_admin.refund_handle,
                    data: {
                        order_refund_id: that.detail.id,
                        type: that.detail.type,
                        is_agree: 1,
                        refund_price: that.refund_price,
                        merchant_remark: that.about
                    },
                    method: 'post'
                }).then(response => {
                    uni.hideLoading();
                    if (response.code === 0) {
                        uni.showModal({
                            title: '提示',
                            content: response.msg,
                            showCancel: false,
                            success: function (res) {
                                if (res.confirm) {
                                    that.notRefund = false;
                                    that.openAddress = false;
                                    that.isRefund = false;
                                    that.addressId = 0;
                                    that.page = 1;
                                    uni.showLoading({
                                        mask: true,
                                        title: '加载中...'
                                    });
                                    that.getList();
                                }
                            }
                        });
                    } else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }).catch(response => {
                    uni.hideLoading();
                    uni.showToast({
                        title: response,
                        icon: 'none',
                        duration: 1000
                    });
                });
            } else {
                uni.showToast({
                    title: '退款金额需大于零',
                    icon: 'none',
                    duration: 1000
                });
            }
        },
        refundHandle(type,detail,cancel) {
            if (cancel == 1) {
                this.cancelRefund = true;
            }
            if (type == 1) {
                if(detail.type == 3) {
                    this.refundOnly = true;
                    this.detail = detail;
                }else if(detail.order.send_type != 0){
                    this.cityRefund = true;
                    this.detail = detail;
                }else {
                    if (this.address.length == 0) {
                        this.noAddress = true;
                    } else {
                        this.detail = detail;
                        this.openAddress = true;
                    }
                }
            } else if (type == 2) {
                this.detail = detail;
                this.notRefund = true;
            }
        },

        toAgreeCancel(e) {
            this.detail = e;
            this.isReason = !this.isReason;
            this.noRefund = false;
        },
        decline(type) {
            let that = this;
            let para = {
                order_refund_id: that.detail.id,
                type: that.detail.type,
                is_agree: type != 0 ? 1 : 0,
                refund_price: that.detail.refund_price,
                merchant_remark: that.about
            }
            if (type == 1) {
                if (that.addressId < 1) {
                    uni.showToast({
                        title: '请选择地址',
                        icon: 'none',
                        duration: 1000
                    });
                    return false;
                }else {
                    para.address_id = that.addressId
                }
            }
            if(type == 4) {
                para.refund = 1
            }
            if (that.cancelRefund) {
                type = 2;
                para.is_agree = 2
            }
            uni.showLoading({
                mask: true,
                title: '处理中...'
            });
            that.$request({
                url: that.$api.app_admin.refund_handle,
                data: para,
                method: 'post'
            }).then(response => {
                uni.hideLoading();
                if (response.code === 0) {
                    uni.showModal({
                        title: '提示',
                        content: response.msg,
                        showCancel: false,
                        success: function (res) {
                            if (res.confirm) {
                                that.page = 1;
                                that.notRefund = false;
                                that.cancelOrder = false;
                                that.openAddress = false;
                                that.refundOnly = false;
                                that.cityRefund = false;
                                that.addressId = 0;
                                that.getList();
                            }
                        }
                    });
                } else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                uni.hideLoading();
                uni.showToast({
                    title: response,
                    icon: 'none',
                    duration: 1000
                });
            });
        },
        chooseAddress(e) {
            if (this.addressId == e) {
                this.addressId = ''
            }else {
                this.addressId = e
            }
        },
        chooseDelivery(e) {
            if (this.getDeliveryId == e.id) {
                this.deliveryId = ''
            }else {
                this.deliveryId = e.id
                this.delivery = "(" + e.id + ")" + e.name;
            }
        },
        noCancel() {
            let that = this;
            uni.showLoading({
                mask: true,
                title: '处理中...'
            });
            that.$request({
                url: that.$api.app_admin.cancel,
                data: {
                    status: 2,
                    remark: that.about,
                    order_id: that.detail.id
                },
                method: 'post'
            }).then(response => {
                uni.hideLoading();
                if (response.code === 0) {
                    uni.showToast({
                        title: response.msg,
                        type: 'success',
                        mask: false,
                        duration: 2000
                    });
                    that.page = 1;
                    that.noRefund = false;
                    that.isReason = false;
                    that.getList();
                } else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                uni.hideLoading();
                uni.showToast({
                    title: response,
                    icon: 'none',
                    duration: 1000
                });
            });
        },
        beNotRefund(e) {
            this.detail = e;
            this.isReason = !this.isReason
            this.noRefund = !this.noRefund;
        }
    },
    onShow: function() {
        this.page = 1;
        this.getList();
    },
    onLoad(options) { this.$commonLoad.onload(options);
        let that = this;
        that.$showLoading({
            type: 'global',
            text: '加载中...'
        });
        that.status = 1;
        that.num = '-1';
        let myDate = new Date();
        // 今天
        let year = myDate.getFullYear();
        let month = myDate.getMonth() + 1;
        if (month >= 1 && month <= 9) {
            month = "0" + month;
        }
        let now = myDate.getDate();
        that.today = year + "-" + month + "-" + now;
        let timestamp = Date.parse(new Date());
        // 昨天
        let yesterTime = (timestamp / 1000 - 24 * 60 * 60) * 1000;
        let yesterDate = new Date(yesterTime)
        let yester_year = yesterDate.getFullYear();
        let yester_month = yesterDate.getMonth() + 1;
        if (yester_month >= 1 && yester_month <= 9) {
            yester_month = "0" + yester_month;
        }
        let yester_now = yesterDate.getDate();
        that.yesterday = yester_year + "-" + yester_month + "-" + yester_now;
        // 7天
        let weekTime = (timestamp / 1000 - 24 * 60 * 60 * 7) * 1000;
        let weekDate = new Date(weekTime)
        let week_year = weekDate.getFullYear();
        let week_month = weekDate.getMonth() + 1;
        if (week_month >= 1 && week_month <= 9) {
            week_month = "0" + week_month;
        }
        let week_now = weekDate.getDate();
        that.weekday = week_year + "-" + week_month + "-" + week_now;

        that.$request({
            url: that.$api.app_admin.delivery
        }).then(response=>{
            if(response.code === 0) {
                this.cityList = response.data.list;
                this.deliveryId = response.data.list[0].id;
                this.delivery = "(" + response.data.list[0].id + ")" + response.data.list[0].name
            }else {
                uni.showToast({
                    title: response.msg,
                    icon: 'none',
                    duration: 1000
                });
            }
        }).catch(response => {
            uni.hideLoading();
        });
        if(options.msg) {
            that.msg = true;
        }
        if(options.status > -1) {
            that.num = options.status.toString();
        }
        if(options.refund > 0) {
            that.status = 2
        }
        if (options.no) {
            if (options.no.slice(0, 2) === "RE") {
                that.status = 2;
            }
            that.search = true;
            that.inSearch = true;
            that.keyword = options.no;
            this.getList();
        }
    }
}
